<!--模板部分-->
<template>
  <div class="container">
    <h1>hello，world！</h1>
    <p>{{test}}</p>
    <p>接收的参数id:
      <span class="hint">{{id}}</span>
    </p>
    <p>接收的参数userid:
      <span class="hint">{{userid}}</span>
    </p>
    <p>接收的参数name:
      <span class="hint">{{username}}</span>
    </p>
    <div class="subrouter">
      <h2 class="hint">子路由：</h2>
      <router-view/>
    </div>
  </div>
</template>
<!--js部分-->
<script>
export default {
  name: 'Helloworld',
  data() {
  	console.log(this.$route);
    return {
      test: '这是一个测试',
      id: this.$route.params.id,//接收参数
      userid: this.$route.query.userid,
      username: this.$route.query.name
    }
  }
}
</script>
<!--样式部分-->
<style>
.container {
  background: #000;
  color: greenyellow;
  height: 500px;
}

.hint {
  color: red;
  font-weight: bold;
}

.subrouter {
  background: #fff;
  width: 500px;
  height: 100px;
  margin: 0 auto;
}
</style>


