import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Helloworld from '@/components/Helloworld'//自定义组件
// 引入子页面
import info from '@/components/subpage/info.vue'
import content from '@/components/subpage/content.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/Helloworld/:id',
      name: 'Helloworld',
      component: Helloworld,
      children: [{
          path: 'info/:id',
          component: info
        },
         {
          path: 'content/:id',
          component: content
        }
      ]
    }
  ]
})



